<template>
  <view class="container">
    <view class="status">
      <text class="status-text">当前索引: {{ currentIndex }}</text>
    </view>
    
    <swiper
      class="swiper"
      :indicator-dots="true"
      :autoplay="false"
      :interval="3000"
      :duration="500"
      :current="currentIndex"
      @change="handleChange"
    >
      <swiper-item v-for="(item, index) in images" :key="index" class="swiper-item">
        <view class="image-container" :style="{ backgroundColor: item.color }">
          <text class="image-text">图片 {{ index + 1 }}</text>
        </view>
      </swiper-item>
    </swiper>
    
    <view class="controls">
      <button @click="prevImage" :disabled="currentIndex <= 0">上一张</button>
      <button @click="nextImage" :disabled="currentIndex >= images.length - 1">下一张</button>
    </view>
    
    <view class="debug-info">
      <text class="debug-title">滑动日志:</text>
      <scroll-view scroll-y class="debug-log">
        <view v-for="(log, index) in logs" :key="index" class="log-item">
          {{ log }}
        </view>
      </scroll-view>
    </view>
  </view>
</template>

<script setup>
import { ref } from 'vue';

// 模拟图片数据
const images = [
  { color: '#FF6B6B' },
  { color: '#4ECDC4' },
  { color: '#FFD166' },
  { color: '#6B5B95' },
  { color: '#88D8B0' }
];

const currentIndex = ref(0);
const logs = ref([]);

// 添加日志
const addLog = (message) => {
  const timestamp = new Date().toLocaleTimeString();
  logs.value.unshift(`${timestamp}: ${message}`);
  
  // 限制日志数量，避免占用过多内存
  if (logs.value.length > 20) {
    logs.value = logs.value.slice(0, 20);
  }
};

// 处理swiper切换事件
const handleChange = (e) => {
  const oldIndex = currentIndex.value;
  currentIndex.value = e.detail.current;
  
  addLog(`滑动切换: ${oldIndex} -> ${currentIndex.value}`);
};

// 按钮控制
const prevImage = () => {
  if (currentIndex.value > 0) {
    currentIndex.value--;
    addLog(`按钮切换: 上一张 (${currentIndex.value})`);
  }
};

const nextImage = () => {
  if (currentIndex.value < images.length - 1) {
    currentIndex.value++;
    addLog(`按钮切换: 下一张 (${currentIndex.value})`);
  }
};
</script>

