  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 20rpx;
}

.status {
  margin-bottom: 20rpx;
  text-align: center;
}

.status-text {
  font-size: 32rpx;
  font-weight: bold;
}

.swiper {
  height: 400rpx;
  margin-bottom: 30rpx;
}

.swiper-item {
  width: 100%;
  height: 100%;
}

.image-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-text {
  font-size: 48rpx;
  color: white;
  font-weight: bold;
}

.controls {
  display: flex;
  justify-content: space-around;
  margin-bottom: 30rpx;
}

.controls button {
  width: 200rpx;
}

.debug-info {
  flex: 1;
  border: 1px solid #eee;
  border-radius: 8rpx;
  padding: 20rpx;
  display: flex;
  flex-direction: column;
}

.debug-title {
  font-size: 28rpx;
  font-weight: bold;
  margin-bottom: 10rpx;
}

.debug-log {
  flex: 1;
  height: 0;
}

.log-item {
  font-size: 24rpx;
  padding: 10rpx 0;
  border-bottom: 1px solid #f5f5f5;
}
</style>